.CartItems{
    margin: 100px 170px;
}
.CartItems hr{
    height: 3px;
    background: #e2e2e2;
    border: 0;
}
.cartitem-format-main{
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr  1fr 1fr 1fr;
    align-items: center;
    gap: 75px;
    padding: 20px 0px;
    color: #454545;
    font-size: 18px;
    font-weight: 600;
}
.cartitems-format{
    font-size: 17px;
    font-weight: 500;
}
.cartitem-product-icon{
    height: 62px;
}
.cartitem-remove-icon{
    width: 15px;
    margin: 0px;
    cursor: pointer;
}
.cartitem-quantity{
    width: 64px;
    height: 50px;
    border: 2px solid #ebebeb;
    background: #fff;
}
.cartitems-down{
    display: flex;
    margin: 100px 0px;
}
.cartitem-total{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 200px;
    gap: 40px;
    color: black;
}
.cartitem-total-item{
    display: flex;
    justify-content: space-between;
    padding: 15px 0px ;
}
.cartitem-total button{
 width: 262;
 height: 58px;
 outline: none;
 border: none;
 background: #ff5a5a;
 color: #fff;  
 font-size: 16px;
 font-weight: 600;
 cursor: pointer;  
}
.cartitems-promocode{
    flex: 1;
    font-size: 16px;
    font-weight: 500;
}
.cartitems-promocode p{
    color: #555;
}
.cartitem-promobox{
    width: 504px;
    margin-top: 15px;
    padding-left: 20px;
    height: 58px;
    background: #eaeaea;
}
.cartitem-promobox input{
    border: none;
    outline: none;
    background: transparent;
    color: rgb(24, 2, 2);
    cursor: pointer;
}
.cartitem-promobox button {
    width: 170px;
    height: 58px;
    font-size: 16px;
    background: black;
    color: white;
    cursor: pointer;
}